#create-component {
    .colors {
        .d-flex {
            display: flex;

            label {
                margin-right: .65rem;
            }

            .black,
            .blue {
                width: 20px;
                height: 20px;
                border-radius: 50%;
                transition: all 200ms ease;
                cursor: pointer;

                &:hover {
                    transform: scale(1.1);
                }
            }

            .black {
                background-color: black;
                margin-right: .65rem;
            }

            .blue {
                background-color: #007DFB;
            }
        }
    }
}